.locationContainer {
    min-height: 200px;
    width: 100%;
    justify-content: center;
    display: flex;
}

.location-map {
    /* float: left; */
    /* width: 100%; */
    /* background: rgba(0, 0, 0, 0) url(../../public/map-image.png) no-repeat scroll center top !important; */
    height: auto;
    border: 2px #fff solid;
}

.addressParagraph {
    color: #fff;
    font-family: Montserrat, sans-serif;
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
    text-align: center;
}

.direction {
    color: #f9b654;
    font-family: Montserrat, sans-serif;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
}

.directionButton {
    color: #f9b654;
    background: #000;
    font-family: Montserrat, sans-serif;
    font-size: 18px;
}